<template><section><h2>Button 按钮</h2>
<h3>基础按钮</h3>
<template>
  <div class="introduce">
    <z-button @click="test">default</z-button>
  </div>
  <div class="introduce">
    <z-button type="primary">primary</z-button>
  </div>
  <div class="introduce">
    <z-button type="success">success</z-button>
  </div>
  <div class="introduce">
    <z-button type="info">info</z-button>
  </div>
  <div class="introduce">
    <z-button type="warning">warning</z-button>
  </div>
  <div class="introduce">
    <z-button type="danger">danger</z-button>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>&gt;</span>default<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">z-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>primary<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>
</code></pre>
<h3>加载状态</h3>
<template>
<z-button type="warning" loading="">加载中...</z-button>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">loading</span>&gt;</span>加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>
</code></pre>
<h3>禁用状态</h3>
<template>
<z-button disabled>满 &yen;199 起送</z-button>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span> <span class="hljs-attr">disabled</span>&gt;</span>满 &amp;yen;199 起送<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>
</code></pre>
<h3>API</h3>
<p>注：加载状态和禁用状态下按钮将不会触发事件。
<template></template></p>
  <div class="introduce-block">
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['type','String','否','default','按钮样式(default,primary,success,info,warning,danger)'],
            ['loading','Boolean','否','false','是否显示加载状态'],
            ['disabled','Boolean','否','false','是否禁用按钮']
          ]">
    </z-table>
  </div>


</section></template>

<script>
export default {
  methods: {
    test() {
      alert(1)
    }
  }
}
</script>


