<template><section><h2>Switch 开关</h2>
<h3>基础开关</h3>
<p>默认状态：</p>
<template>
  <z-switch @change="getIsOpen"></z-switch>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-switch</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getIsOpen"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-switch</span>&gt;</span>
</code></pre>
<p>初始开启状态：</p>
<template>
  <z-switch default-checked="" @change="getIsOpen"></z-switch>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-switch</span> <span class="hljs-attr">default-checked</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"getIsOpen"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-switch</span>&gt;</span>
</code></pre>
<p>注： 点击开关组件将会报告开关的状态，监听change事件即可。</p>
<pre><code class="language-js">methods: {
  getIsOpen(isOpen) {
    <span class="hljs-keyword">if</span> (isOpen) {
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'开关状态：打开'</span>)
    }
    <span class="hljs-keyword">else</span>{
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'开关状态：关闭'</span>)
    }
  }
}
</code></pre>
<h3>禁用状态</h3>
<p>默认禁用状态：</p>
<template>
  <z-switch disabled></z-switch>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-switch</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-switch</span>&gt;</span>
</code></pre>
<p>禁用且开启状态：</p>
<template>
  <z-switch disabled default-checked="" :off-text="'开'"></z-switch>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-switch</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">default-checked</span> <span class="hljs-attr">:off-text</span>=<span class="hljs-string">"'开'"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-switch</span>&gt;</span>
</code></pre>
<p>注：禁用状态下开关将不会触发鼠标事件。</p>
<h3>API</h3>
<template>
  <div class="introduce-block">
    <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
            ['default-checked','Boolean','否','false','开关初始是否开启'],
            ['disabled','Boolean','否','false','是否禁用开关'],
            ['width','Number','否','50','开关宽度'],
            ['height','Number','否','25','开关高度'],
            ['bgc','String','否','rgb(65, 184, 131)','开启时的背景色'],
            ['on-text','String','否','-','开启时显示的文字'],
            ['off-text','String','否','-','关闭时显示的文字'],
            ['name','String','否','-','switch对应的name(适用于表单提交)']
          ]">
    </z-table>
  </div>
</template>
<h3>EVENT</h3>
<template>
  <div class="introduce-block">
    <z-table :ths="['事件名称', '说明', '回调参数']" :trs="[
            ['change','switch状态发生变化时的回调函数','新状态的布尔值']
          ]">
    </z-table>
  </div>
</template>

</section></template>

<script>
export default {
  methods: {
    getIsOpen(isOpen) {
      if (isOpen) {
        console.log('开关状态：打开')
      }
      else{
        console.log('开关状态：关闭')
      }
    }
  }
}
</script>


