<template><section><h2>Dimmer 遮罩层</h2>
<template>
  <z-button type="danger" @click="show = true">点击制造雾霾</z-button>
<p><z-dimmer :show="show" @click="show = false"></z-dimmer>
</p></template><p></p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"show = true"</span>&gt;</span>点击制造雾霾<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">z-dimmer</span> <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"show = false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">z-dimmer</span>&gt;</span>
</code></pre>
<pre><code class="language-js">data() {
  <span class="hljs-keyword">return</span> {
    <span class="hljs-attr">show</span>: <span class="hljs-literal">false</span>
  }
}
</code></pre>

<p>注：遮罩层显示状态下将会自动禁用PC端和移动端页面滚动（不影响遮罩层之上的浮层滚动）。</p>
<h3>API</h3>
<template>
  <z-table :ths="['参数','类型','必填','默认值','说明']" :trs="[
    ['show','Boolean','是','-','是否显示遮罩层']
  ]">
  </z-table>
</template>
</section></template>

<script>
export default {
  data() {
    return {
      show: false
    }
  }
}
</script>


